<style lang="stylus" scoped>
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	$orange = rgb(255, 96, 48)
	$green = rgb(180, 234, 102)
	$blue = rgb(48, 96, 255)
	.news
		overflow hidden
	.item
		position relative
		float left
		box-sizing border-box
		width 20rem
		height 6rem
		padding .625rem .5rem .625rem 6rem
		background-position .5rem .5rem
		background-repeat no-repeat
		background-size 5rem 4rem
		border-bottom 1px solid $grey3
		h1
			height 1rem
			overflow hidden
			white-space nowrap
			text-overflow ellipsis
			font-size @height
			color $black1
		h2
			margin-top .25rem
			display -webkit-box
			height 2.4rem
			overflow hidden
			word-break break-all
			text-overflow ellipsis
			-webkit-line-clamp 2
			-webkit-box-orient vertical
			font-size .8rem
			line-height 1.2rem
			color $black2
		p
			position absolute
			bottom .5rem
			font-size .675rem
			color $grey4
			em
				color $blue
				&:first-of-type
					margin-right .2rem
				&:last-of-type
					margin-left .2rem
	.more
		float left
		width 20rem
		text-align center
		font-size .8rem
		line-height 3
		color $black2
</style>
<template>
	<div class="news">
		<router-link v-for="item in news" :to="`/news/${item._id}`" class="item" :style="{backgroundImage:`url(${item.file})`}">
			<h1>{{item.title}}</h1>
			<h2>{{item.title.replace(/(.*)/, "$1，$1，$1。")}}</h2>
			<p><em>{{item.modifiedTime.replace(/T(.{5}).*/, " $1")}}</em>来自<em>{{item.keyword.split(/\s,/)[0]}}</em></p>
		</router-link>
		<p class="more">下拉获取更多</p>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				news : [],
				unitY : 0
			};
		},
		async beforeCreate(){
			const {data} = await (await fetch("/api/getNews/1")).json();
			let times = 1;
			this.$parent.footer = 1;
			this.news = [...data, ...data];
			onscroll = async () => {
				if(scrollY >= this.unitY * times){
					times++;
					this.news = this.news.concat((await (await fetch(`/api/getNews/${times}`)).json()).data);
				}
			};
		},
		updated(){
			if(!this.unitY){
				this.unitY = this.$el.offsetHeight - innerHeight + innerWidth / 20 * 5;
			}
		}
	};
</script>


